import React, { useState, useEffect } from 'react';
import './PromotionBanner.css';
import promotionBanner1 from '../../assets/images/promotionBanner1.png';
import promotionBanner2 from '../../assets/images/banner_productos.png';
import promotionBanner3 from '../../assets/images/promotionBanner3.png';

const banners = [
  {
    img: promotionBanner1,
    alt: 'Promo 1',
    title: 'Belleza que ilumina',
    buttonText: 'COMPRAR',
    buttonLink: '/productos'
  },
  {
    img: promotionBanner2,
    alt: 'Promo 2',
    title: 'Lo nuevo para vos',
    buttonText: 'DESCUBRIR',
    buttonLink: '/lanzamientos'
  },
  {
    img: promotionBanner3,
    alt: 'Promo 3',
    title: 'Promos destacadas',
    buttonText: 'VER TODO',
    buttonLink: '/promociones'
  }
];


const PromotionBanner = () => {
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % banners.length);
    }, 3000);

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="promotion-banner-container">
      <div className="promotion-banner">
        <img
          src={banners[currentBannerIndex].img}
          alt={banners[currentBannerIndex].alt}
          className={`promotion-banner-image ${fade ? 'fade-in' : 'fade-out'}`}
        />
        <div className="banner-content">
          <h2>{banners[currentBannerIndex].title}</h2>
          <a href={banners[currentBannerIndex].buttonLink} className="banner-button">
            {banners[currentBannerIndex].buttonText}
          </a>
        </div>
      </div>
    </div>
  );
};

export default PromotionBanner;
